var assistLimitConst = require("./constants/assistLimitConst.json");

const assistLimitStats = () => {
    const getStats = (character) => {
        return getChar(character);
    };

    const getChar = (character) => {
        const map4 = {
            // 4 Stars
            str: assistLimitConst["4stars"]["str"],
            end: assistLimitConst["4stars"]["end"],
            dex: assistLimitConst["4stars"]["dex"],
            agi: assistLimitConst["4stars"]["agi"],
            mag: assistLimitConst["4stars"]["mag"],
            free: assistLimitConst["4stars"]["free"],

            // 4 Stars Plus
            str_plus: assistLimitConst["4plus"]["str"],
            end_plus: assistLimitConst["4plus"]["end"],
            dex_plus: assistLimitConst["4plus"]["dex"],
            agi_plus: assistLimitConst["4plus"]["agi"],
            mag_plus: assistLimitConst["4plus"]["mag"],
            free_plus: assistLimitConst["4plus"]["free"],
        };

        const map3 = {
            // 3 Stars
            str: assistLimitConst["3stars"]["str"],
            end: assistLimitConst["3stars"]["end"],
            dex: assistLimitConst["3stars"]["dex"],
            agi: assistLimitConst["3stars"]["agi"],
            mag: assistLimitConst["3stars"]["mag"],
        };
        const map3free = {
            // 3 Stars Free
            str: assistLimitConst["3free"]["str"],
            mag: assistLimitConst["3free"]["mag"],
        };
        const map2 = {
            // 2 Stars
            str: assistLimitConst["2stars"]["str"],
            end: assistLimitConst["2stars"]["end"],
            dex: assistLimitConst["2stars"]["dex"],
            agi: assistLimitConst["2stars"]["agi"],
            mag: assistLimitConst["2stars"]["mag"],
        };

        const map1 = {
            // 1 Stars
            str: assistLimitConst["1star"]["str"],
        }

        let res;

        if (character.is_free) {
            switch (character.stars) {
                case 4:
                    res = map4[character.type_stat] ?? [];
                    break;
                case 3:
                    res = map3free[character.type_stat] ?? [];
                    break;
                default:
                    res = assistLimitConst.empty;
                    break;
            }
        } else {
            switch (character.stars) {
                case 4:
                    res = map4[character.type_stat] ?? [];
                    break;
                case 3:
                    res = map3[character.type_stat] ?? [];
                    break;
                case 2:
                    res = map2[character.type_stat] ?? [];
                    break;
                case 1:
                    res = map1[character.type_stat] ?? [];
                    break;
                default:
                    res = assistLimitConst.empty;
                    break;
            }
        }

        return res;
    };
    return { getStats };
};

export default assistLimitStats;
