var HpMpTable = require("./constants/assistHPMPConstants.json");

const assistLimitHpMp = () => {
    const getHpMp = (character) => {

        switch (character.hp_mp_growth) {
            case "normal":
                return HpMpTable.normal;
            case "free":
                return HpMpTable.free;
            case "magic":
                return HpMpTable.magic;
            default:
                return HpMpTable.empty;
        }
    };

    return { getHpMp };
};

export default assistLimitHpMp;
