<template>
  <div class="main_char">
    <div class="char_page" :style="screenWidth >= 768 ? {width: '75%', minWidth: '640px', maxWidth: '60%'} : {width: '95%', minWidth: '320px', maxWidth: '640px'}">
      <div class="arrowCharStyle" @click="goBack()">
        <img alt="Go Back" class="arrowImage" src="../assets/left_arrow.png" />
      </div>
      <div class="adventureStyle">
        <img alt="Adventurer" class="adventureTag" src="../assets/ast_banner.png" />
      </div>
      <div v-if="error" class="errorPag">
        <n-result status="error" title="Error">
          <p>{{ errorMessage }}</p>
        </n-result>
      </div>
      <div v-if="character" class="info_page">
        <div class="adventureInfo">
          <n-space vertical>
            <n-space vertical id=adventureInfo_title>
              <span>[{{character.title}}]</span>
              <span>{{ character.name }}</span>
            </n-space>
            <div class="adv_image" @click="showModel = true">
              <img alt="Frame" :src="decideBase(character.stars)">
              <!-- <img :alt="character.id_intern" :src="getImg(character.id_intern)"> -->
              <img alt="Frame_front" :src="decideBaseFrame(character.stars)">
            </div>
          </n-space>
          <!-- <n-button id="reportProblem" type=error @click="gotoReport">Report Problem</n-button> -->
        </div>
        <div class="char_wrapper">
          <div class="char_image" v-if="screenWidth >= 768">
            <!-- <img id="char_rectangle" alt="CharRectangle" :src="getRec(character.id_intern)" @click="showModel = true" /> -->
          </div>
          <n-modal v-model:show="showModel">
            <n-card
              style="width: auto; height: 50%; position: relative; justify-content: center;"
              :bordered="false"
              size="huge"
              role="dialog"
              aria-modal="true"
            >
              <n-image
                :src="getModel(character.id_intern)"
                width="400"
                id="char_model"
              >
              </n-image>
              <p id="char_text">Click on the image to zoom</p>
              <n-button id="char_slider_button" type="error" @click="showModel = false">
                Close
              </n-button>
            </n-card>
          </n-modal>
          <div class="information_section" :style="screenWidth >= 768 ? {borderLeftWidth: '1px', width: '75%'} : {borderLeftWidth: '0px', width: '100%'}">
            <n-space vertical>
              <div class="attr_level_section" :style="screenWidth >= 768 ? {flexDirection: 'row', paddingLeft: '20px'} : {flexDirection: 'column', paddingLeft: '0px'}">
                <div id="elementPage" :style="screenWidth >= 768 ? {width: '36%'} : {width: '96%'}">
                  <n-space vertical>
                    <div id="elementWrapper">
                      <img id="elementImage" :alt="character.type_attack" :src="decideStatImage(character.type_stat)" width="32" height="32"/>
                      <span>{{ decideStat(character.type_stat) }}</span>
                    </div>
                    <div id="elementWrapper">
                      <!-- <img :alt="character.type_element" :src="decideElement(character.type_element)" width="21" height="21" /> -->
                      Has Repeat: {{ decideTrueFalse(character.have_repeat) }}
                    </div>
                    <div id="elementWrapper" v-if="character.time_limited">
                      <!-- <span>Time limited: {{ decideTrueFalse(character.time_limited) }}</span> -->
                      <img :alt="character.time_limited" src="../assets/attacktype/timelimited.png" />
                    </div>
                    <div id="elementWrapper" v-if="character.is_free">
                      <!-- <span>Free Unit: {{ decideTrueFalse(character.is_free) }}</span> -->
                      <img :alt="character.is_free" src="../assets/attacktype/freeunit.png" />
                    </div>
                  </n-space>
                </div>
                <n-space vertical id="levelPage" :style="screenWidth >= 768 ? {width: '60%', margin: '0px 0px 0px'} : {width: '100%', margin: '40px 0px 0px'}">
                  <div id="minmaxStats">
                    <n-button type="default" id="minmaxButtons" @click="changeMaxMin(true)">
                      Min
                    </n-button>
                    <n-button type="default" id="minmaxButtons" @click="changeMaxMin(false)">
                      Max
                    </n-button>
                  </div>
                  <!-- :style="screenWidth >= 768 ? {width: '220px'} : {width: '135px'}" -->
                  <n-space vertical id="limitbreakSection">
                    <span>Limit Break Level</span>
                    <div id="limitbreakCounter">
                      <n-button @click="changeLimit(-1)">-</n-button>
                      <img id="counterImage" :alt="character.id_intern" :src="decideLimit()" @click="clickLimit" ref="limitLevel" />
                      <n-button @click="changeLimit(1)" >+</n-button>
                    </div>
                  </n-space>
                </n-space>
              </div>
              <n-space vertical class="falna_window">
                <n-space vertical class="statusPage">
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/hpbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.hp + limitbreakValueHpMp[0] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakValueHpMp[0] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/mpbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.mp + limitbreakValueHpMp[1]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakValueHpMp[1] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/atkbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.str + limitbreakDisplay[0] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[0] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/magbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.mag + limitbreakDisplay[4] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[4] }}</span>
                    </div>
                  </div>
                  <div id="statusBar">
                    <img :alt="character.id_intern" src="../assets/status/defbar.png" />
                    <div id="status_text">
                      <span id="mainStat" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.end + limitbreakDisplay[1] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[1] }}</span>
                    </div>
                  </div>
                </n-space>
                <n-space vertical class="falnaPage">
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Str.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[0]" :src="getFalnaImages(falnaImages[0])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.str + limitbreakDisplay[0] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[0] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">End.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[1]" :src="getFalnaImages(falnaImages[1])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.end + limitbreakDisplay[1]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[1] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Dex.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[2]" :src="getFalnaImages(falnaImages[2])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.dex + limitbreakDisplay[2]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[2] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Agi.</span>
                      <div  id="falnaStatus">
                        <img :alt="falnaImages[3]" :src="getFalnaImages(falnaImages[3])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.agi + limitbreakDisplay[3]}}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[3] }}</span>
                    </div>
                  </div>
                  <div id="falnaBar">
                    <div id="falna_text">
                      <span id="mainFalnaLabel">Mag.</span>
                      <div id="falnaStatus">
                        <img :alt="falnaImages[4]" :src="getFalnaImages(falnaImages[4])" />
                      </div>
                      <span id="mainFalnaValue" :style="screenWidth >= 768 ? {right: '65px'} : {right: '10px'}">{{ character.mag + limitbreakDisplay[4] }}</span>
                      <span id="limitStat" v-if="screenWidth >= 768"> + {{ limitbreakDisplay[4] }}</span>
                    </div>
                  </div>
                </n-space>
              </n-space>
            </n-space>
          </div>
        </div>
        <n-space vertical id="skillSection">
          <div id="skillSwitchWrapper">
            <span v-if="!skSwitch">Skill +</span>
            <span v-else>Skill ++</span>
            <div id="skillSwitch" @click="(skSwitch=!skSwitch)" :class="skSwitch ? 'rainbow_border' : {}">
              <div id="skillSwitchButton" :style="skSwitch ? {left: '64px'} : {left: '2px'}"></div>
            </div>
          </div>
          <div id="skill_wrapper">
            <span>Assist Skills</span>
            <div id="skill_section">
              <n-card id="effect_cards" @click="(skillDropdown = !skillDropdown)"> 
                <div id="effect_card_title">
                  <div id="effect_card_skill_name">
                    <span v-if="!skSwitch">
                      {{ character.skill_name }} +
                    </span>
                    <span v-else>
                      {{ character.skill_name }} ++
                    </span>
                  </div>
                </div>
                <div id="effect_card_raw" v-if="!skSwitch">{{ character.raw_1 }}</div>
                <div id="effect_card_raw" v-else>{{ character.raw_2 }}</div>
                <div id="effect_card_arrow" 
                  :style="{
                    clipPath: skillDropdown ? 'polygon(50% 100%, 0 0, 100% 0)' : 'polygon(50% 0, 0 100%, 100% 100%)'
                  }"
                  >
                </div>
                <Transition name="skill_effect">
                  <div id="effect_card_modal" v-if="skillDropdown">
                    <div id="effect_card_attacks" v-if="Object.keys(character.effect).length !== 0">
                      <n-divider title-placement="Center">Effect</n-divider>
                      <n-card size="small" id="effect_card_box_effect" v-for="effect in character.effect.n_effects" :key="effect" @click.stop="clickEffect(character, effect)"
                        :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}">
                        <span>[{{ character.effect["level1_effect" + effect + "_target"]}}]&nbsp;</span>
                        <span>{{ character.effect["level1_effect" + effect + "_attr"]}}&nbsp;</span>
                        <span v-if="!skSwitch">{{ character.effect["level1_effect" + effect + "_mod"]}}&nbsp;</span>
                        <span v-else>{{ character.effect["level2_effect" + effect + "_mod"]}}&nbsp;</span>
                      </n-card>
                    </div>
                    <div v-if="character.have_repeat">
                      <n-divider title-placement="Center">Instant Effects</n-divider>
                      <div style="margin-top: 24px">
                        <span v-if="!skSwitch">When Attacked by: {{ character.repeat_effect["when_attacked_by"] }}</span>
                        <span v-else>When Attacked by: {{ character.repeat_effect["when_mod"] }}</span>
                        <br />
                        <span v-if="!skSwitch">+ {{ character.repeat_effect["howmany"] }} Instant Effects</span>
                        <span v-else>+ {{ character.repeat_effect["howmany2"] }} Instant Effects</span>
                        <br />
                        <span>on {{ character.repeat_effect["effect_who"] }}</span>
                        <br />
                        <span> {{ character.repeat_effect["perturn"] }} per turn </span>
                        <n-card size="small" id="instant_effect_box" v-for="repeat in character.repeat_effect.n_effects" :key="repeat" @click.stop="clickRepeat(character, repeat)"
                          :style="screenWidth >= 768 ? {width: 'fit-content'} : {width: '100%'}">
                          <span>{{ character.repeat_effect["effect" + repeat] }}&nbsp;</span>
                          <span v-if="character.repeat_effect['effect' + repeat + '_value']">
                            <span v-if="!skSwitch"> {{ character.repeat_effect['effect' + repeat + '_value']}}</span>
                            <span v-else> {{ character.repeat_effect['effect' + repeat + '_mod']}}</span>
                          </span>
                          <span v-if="character.repeat_effect['effect' + repeat + '_turns']">/ {{ character.repeat_effect['effect' + repeat + '_turns'] }} turn(s)</span>
                          <span v-if="character.repeat_effect['effect' + repeat + '_chance']"> {{ character.repeat_effect['effect' + repeat + '_chance'] }} chance</span>
                        </n-card>
                      </div>
                    </div>
                  </div>
                </Transition>
              </n-card>
            </div>
          </div>
        </n-space>
      </div>
    </div>
  </div>
</template>

<script>
import {  
  NButton,
  NSpace,
  NSelect,
  NResult,
  NModal,
  NImage,
  NCard,
  NDivider,
  NCollapse,
  NCollapseItem,
} from 'naive-ui'
import axios from 'axios'
import numberToRank from '@/composables/numberToRank'
import assistLimitStats from '@/composables/assistLimitStats'
import assistLimitHpMp from '@/composables/assistLimitHpMp'
import { ref } from '@vue/reactivity'

export default {
  props: ["id"],
  data() {
    return {
      id_char: this.id,
      screenWidth: window.innerWidth,

      character: null,
      limitbreakStats: null,
      limitbreakHpMp: null,

      error: null,
      errorMessage: "",

      showModel: ref(false),
      skillDropdown: false,

      skSwitch: false,
      
      // STR, END, DEX, AGI, MAG, HP, MP
      limitbreakValue: [0, 0, 0, 0, 0],
      limitbreakDisplay: [0, 0, 0, 0, 0],
      limitbreakValueHpMp: [0, 0],
      limitbreak: 0,

      falnaImages: ["", "", "", "", ""]
    }
  },
  components:{
    NButton,
    NSpace,
    NSelect,
    NResult,
    NModal,
    NImage,
    NCard,
    NDivider,
    NCollapse,
    NCollapseItem
  },
  beforeMount() {
    const { getStats } = assistLimitStats()
    const { getHpMp } = assistLimitHpMp()

    try{
      var ast_order = require('../data/ast_order_id.json')
      let ast_list = window.sessionStorage.getItem("ast_list")
      if(ast_list == null){
        var data = require('../data/ast_list_DATA.json')
        this.dataAst = data
        this.filteredData = data
        window.sessionStorage.setItem("ast_list", JSON.stringify(data));

      }else{
        this.dataAst = JSON.parse(ast_list)
        this.filteredData = JSON.parse(ast_list)
      }
      // axios.get('http://192.168.3.20:8082/assist/' + this.id_char).then(response => {
      // axios.get('http://172.16.30.62:8082/assist/' + this.id_char).then(response => {
      // axios.get('http://localhost:8082/assist/' + this.id_char).then(response => {
        // if(response.statusText != "OK"){
        //   throw new Error('This adventurer does not exist!');
        // }

      this.character = this.dataAst[ast_order[this.id_char]]
      this.limitbreakStats = getStats(this.character)
      this.limitbreakHpMp = getHpMp(this.character)
      this.updateFalnaRank()
      this.onResize()
      // }).catch(err => {
      //   this.error = err
      //   this.errorMessage = "This adventurer does not exist"
      // })
    }
    catch (err){
      this.error = err
      this.errorMessage = "This adventurer does not exist"
    }
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    })
  },

  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
  },
  methods: {
    onResize() {
      this.screenWidth = window.innerWidth
    },
    getImg(url){
      return require('../assets/assists/hex_ast/' + url + '.png')
    },
    getRec(url){
      return require('../assets/assists/h_rectangle_ast/' + url + '.png')
    },
    getModel(url){
      return require('../assets/assists/h_full_ast/' + url + '.png')
    },
    goBack(){
      this.$router.push({ name: "assist_list"});
    },
    clickEffect(char, eff){
      let filter = {
        ef_target: char.effect["level1_effect" + eff + "_target"].toLowerCase().replace(/ /g, "_"),
        ef: char.effect["level1_effect" + eff + "_attr"].toLowerCase().replace(/ /g, "_"),
        // ef_val1: temp.toLowerCase().replace(/ /g, "_").replace("%", "")
      }
      if(!this.skSwitch){
        let tempeff = char.effect["level1_effect" + eff + "_mod"].toLowerCase().replace(/ /g, "_").replace("%", "")
        if(tempeff >= 0){
          filter["ef_val1"] = tempeff
        }else{
          filter["ef_val2"] = tempeff
        }
      }else{
        let tempeff = char.effect["level2_effect" + eff + "_mod"].toLowerCase().replace(/ /g, "_").replace("%", "")
        if(tempeff >= 0){
          filter["ef_val1"] = tempeff
        }else{
          filter["ef_val2"] = tempeff
        }
        filter["effect_lv"] = "true"
      }
      this.$router.push({ name: "filter_ast", query: filter});
    },
    clickRepeat(char, rep){
      let filter = {
        ef: char.effect["effect" + rep].toLowerCase().replace(/ /g, "_"),
        // ef_val1: temp.toLowerCase().replace(/ /g, "_").replace("%", ""),
        filter_repeat_effect: "repeat"
      }
      if(!this.skSwitch){
        let tempeff = char.effect["effect" + rep + "_value"].toLowerCase().replace(/ /g, "_").replace("%", "")
        if(tempeff >= 0){
          filter["ef_val1"] = tempeff
        }else{
          filter["ef_val2"] = tempeff
        }
      }else{
        let tempeff = char.effect["effect" + rep + "_mod"].toLowerCase().replace(/ /g, "_").replace("%", "")
        if(tempeff >= 0){
          filter["ef_val1"] = tempeff
        }else{
          filter["ef_val2"] = tempeff
        }
        filter["effect_lv"] = "true"
      }
      this.$router.push({ name: "filter_ast", query: filter});
    },
    decideBase(star){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/frame_1.png')
          case 2:
            return require('../assets/frame/frame_2.png')
          case 3:
            return require('../assets/frame/frame_3.png')  
          case 4:
            return require('../assets/frame/frame_4_star.png')
          default:
            return require('../assets/frame/frame_1.png')    
        }
      }
    },
    decideBaseFrame(star){
      if(!false){
        switch (star) {
          case 1:
            return require('../assets/frame/base_frame_1.png')
          case 2:
            return require('../assets/frame/base_frame_2.png')
          case 3:
            return require('../assets/frame/base_frame_3.png')  
          case 4:
            return require('../assets/frame/base_frame_4_star.png')
          default:
            return require('../assets/frame/base_frame_1.png')    
        }
      }
    },
    decideStatImage(type_stat){
      let tempstat = ""
      switch (type_stat){
        case "str":
          tempstat = "strength"
          break
        case "str_plus":
          tempstat = "strength"
          break
        case "end":
          tempstat = "endurance"
          break
        case "end_plus":
          tempstat = "endurance"
          break
        case "dex":
          tempstat = "dexterity"
          break
        case "dex_plus":
          tempstat = "dexterity"
          break
        case "agi":
          tempstat = "agility"
          break
        case "agi_plus":
          tempstat = "agility"
          break
        case "mag":
          tempstat = "magic"
          break
        case "mag_plus":
          tempstat = "magic"
          break
        case "free":
          tempstat = "free"
          break
        case "free_plus":
          tempstat = "free"
          break
        default:
          tempstat = "strength"
          break
      }
      return require('../assets/status/' + tempstat + '.png')
    },
    decideStat(type_stat){
      switch(type_stat){
        case "str":
          return 'Strength Type'
        case "str_plus":
          return 'Strength Type'
        case "end":
          return 'Endurance Type'
        case "end_plus":
          return 'Endurance Type'
        case "dex":
          return 'Dexterity Type'
        case "dex_plus":
          return 'Dexterity Type'
        case "agi":
          return 'Agility Type'
        case "agi_plus":
          return 'Agility Type'
        case "mag":
          return 'Magic Type'
        case "mag_plus":
          return 'Magic Type'
        case "free":
          return 'Free Type'
        case "free_plus":
          return 'Free Type'
        default:
          return '??? Type'
      }
    },
    decideTrueFalse(boolea){
      switch(boolea){
        case true:
          return 'Yes'
        case false:
          return 'No'
        default:
          return 'No'
      }
    },
    decideLimit(){
      if (this.limitbreak >= 0 && this.limitbreak <= 5) {
        return require('../assets/status/limit' + this.limitbreak + '.png')
      }
    },
    decideLimitIcon(){
      if (this.limitbreak >= 0 && this.limitbreak <= 5) {
        return require('../assets/status/limitbreak_' + this.limitbreak + '.png')
      }
    },
    changeLimit(add){
      let res = this.limitbreak + add
      if ((res >= 0 && res <= 5)){
        let change = this.limitbreak
        this.limitbreak += add
        if(add >= 1){
          this.limitbreakValue[0] += this.limitbreakStats.str[change]
          this.limitbreakValue[1] += this.limitbreakStats.end[change]
          this.limitbreakValue[2] += this.limitbreakStats.dex[change]
          this.limitbreakValue[3] += this.limitbreakStats.agi[change]
          this.limitbreakValue[4] += this.limitbreakStats.mag[change]
          this.limitbreakValueHpMp[0] += this.limitbreakHpMp.hp[change]
          this.limitbreakValueHpMp[1] += this.limitbreakHpMp.mp[change]
        }else{
          change -= 1
          this.limitbreakValue[0] -= this.limitbreakStats.str[change]
          this.limitbreakValue[1] -= this.limitbreakStats.end[change]
          this.limitbreakValue[2] -= this.limitbreakStats.dex[change]
          this.limitbreakValue[3] -= this.limitbreakStats.agi[change]
          this.limitbreakValue[4] -= this.limitbreakStats.mag[change]
          this.limitbreakValueHpMp[0] -= this.limitbreakHpMp.hp[change]
          this.limitbreakValueHpMp[1] -= this.limitbreakHpMp.mp[change]
        }
        this.updateFalnaRank()
      }
    },
    setLimit(setLimit){
      let tempStats = [0, 0, 0, 0, 0, 0, 0]
      if(setLimit != 0){
        for (let i = 0; i < setLimit; i++){
          tempStats[0] += this.limitbreakStats.str[i]
          tempStats[1] += this.limitbreakStats.end[i]
          tempStats[2] += this.limitbreakStats.dex[i]
          tempStats[3] += this.limitbreakStats.agi[i]
          tempStats[4] += this.limitbreakStats.mag[i]
          tempStats[5] += this.limitbreakHpMp.hp[i]
          tempStats[6] += this.limitbreakHpMp.mp[i]
        }
      }
      // console.log(tempStats)
      tempStats.forEach((stat, index) => {
        if(index <= 4){
          this.limitbreakValue[index] = stat
        }else{
          this.limitbreakValueHpMp[index - 5] = stat
        }
      });
      this.limitbreak = setLimit
      this.updateFalnaRank()
    },
    clickLimit(e){
      let limitX = Math.round(e.clientX - this.$refs.limitLevel.getBoundingClientRect().left)
      if (limitX < 11){
        this.setLimit(0)
      }
      else if (limitX >= 11 && limitX < 36){
        this.setLimit(1)
      }
      else if (limitX >= 36 && limitX < 62){
        this.setLimit(2)
      }
      else if (limitX >= 62 && limitX < 88){
        this.setLimit(3)
      }
      else if (limitX >= 88 && limitX < 114){
        this.setLimit(4)
      }
      else{
        this.setLimit(5)
      }
    },  
    changeMaxMin(ifmin){
      if(ifmin == true){
        this.setLimit(0)
      }else{
        this.setLimit(5)
      }
    },
    updateFalnaRank(){
      const { getAstRank } = numberToRank()

      this.limitbreakDisplay = this.limitbreakValue;

      this.falnaImages = getAstRank([
        this.character.str + this.limitbreakValue[0],
        this.character.end + this.limitbreakValue[1],
        this.character.dex + this.limitbreakValue[2],
        this.character.agi + this.limitbreakValue[3],
        this.character.mag + this.limitbreakValue[4]
      ])
    },
    getFalnaImages(falna){
      return require('../assets/rank/' + falna + '.png')
    },
  },
}
</script>

